import '@servicenow/now-icon';
import '@servicenow/now-button';
import {CONFIRMATION_MSG_SECONDARY_CLICKED, CONFIRMATION_MSG_PRIMARY_CLICKED} from './constants';

export default (state,{dispatch}) => {
	const {properties: {icon,iconStatus,header,headerLevel,moreInfo,
		resolution,caption,primaryActionLabel,secondaryActionLabel}} = state;
	const HeadingLevel = `h${headerLevel}`;

	return (
		<div className="now-card" tabindex="0">
			{
				(icon && icon.length) ? 
					<now-icon class={{
						'now-icon': true,
						['-' + iconStatus]: iconStatus
					}} size="xl" icon={icon} />
					:''}
			{
				header ? 
					<HeadingLevel  className="header-msg">{header}</HeadingLevel>
					:''}
			
			{
				caption ? 
					<div className="text-tertiary">{caption}</div>
					:''}

			{
				moreInfo ? 
					<div className="text-secondary secondary-msg">{moreInfo}</div>
					:''}

			{
				resolution && resolution.label ? 
					<div className="text-secondary res-msg-label">{resolution.label}</div>
					:''}

			{
				resolution && resolution.value ? 
					<div className="text-secondary res-msg-value">{resolution.value}</div>
					:''}

			<div className="now-button-group">
			{
				primaryActionLabel ?
				<now-button
								className="padding-bottom-md primary-action"
								variant="primary"
								label={primaryActionLabel}
								append-to-payload={{event: CONFIRMATION_MSG_PRIMARY_CLICKED}} />
					:''}
			{
				secondaryActionLabel ?
				<now-button
								className="padding-bottom-md secondary-action"
								variant="secondary"
								label={secondaryActionLabel}
								append-to-payload={{event: CONFIRMATION_MSG_SECONDARY_CLICKED}} />
					:''}
			</div>
			
	    </div>
	);
};

