import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import style from './style.scss';
import {NOW_BUTTON_CLICKED,CONFIRMATION_MSG_SECONDARY_CLICKED, CONFIRMATION_MSG_PRIMARY_CLICKED} from './constants';

/**
 * This confirmation could be shown for different flows such as a request, sensitive request, task completions etc., so it needs to be configurable to show the right information based on the completed flow.
 * Users feel more confident when they receive a confirmation page with key information about the flow they just completed.
 *
 * ```jsx
 * <now-confirmation-message></now-confirmation-message>
 * <now-confirmation-message icon="circle-check-outline" iconColor="--now-color--presence-busy" headerMessage="Issue Reported!" 
 * resolutionMessage={label:'Estimated resolution in', value:'2 hours'}  secondaryMessage="An agent will reach out shortly" referenceNumber="INC10001" 
 * primaryButton={label: 'View record', page: 'home', params: {}}
 * secondaryButton={label: 'View full list', page: 'other', params: {}}></now-confirmation-message>
 * ```
 *
 * @seismicElement now-confirmation-message
 * @summary Confirmation message with confirguation for icon and messages
 */
createCustomElement('now-confirmation-message', {
	renderer: {
		type: snabbdom,
	},
	view,
	styles: style,
	properties: {
		/**
		 * If defined, specifies the icon to display at the start.
		 * See the `now-icon` component documentation for valid inputs.
		 * @type {string}
		 */
		icon: {
		},
		/**
		 * Sets the icon color to indicate priority.
		 * @type {string}
		 */
		iconStatus: {
			default: 'info',
			schema: {
				type: 'string',
				enum: [
					'critical',
					'high',
					'moderate',
					'warning',
					'info',
					'positive',
					'low'
				]
			}
		},
		/**
		 * Header message to be displayed.
		 * @type {string}
		 */
		header: {
			required: true, 
			schema: {
				type: 'string'
			}
		},
		/**
		 * HTML heading element number used to render heading. Defaults to '1'
		 * @type {string}
		 */
		headerLevel: {
			default: '1',
			schema: {type: 'string', enum: ['1', '2', '3', '4', '5', '6']}
		},
		/**
		 * If defined, displays the resolution message.
		 * @type {label: string, value: string}
		 */
		resolution: {
			schema: {
				type: 'object'
			}
		},
		/**
		 * If defined, displays the secondary message.
		 * @type {string}
		 */
		moreInfo: {
		},
		/**
		 * If defined, displays the caption .
		 * @type {string}
		 */
		caption: {
			schema: {
				type: 'string'
			}
		},
		/**
		 * Primary action label displayed as button text.
		 * @type {string}
		 */
		primaryActionLabel: {
			schema: {
				type: 'string'
			}
		},
		/**
		 * Secondary action label displayed as button text.
		 * @type {string}
		 */
		secondaryActionLabel: {
			schema: {
				type: 'string'
			}
		}
	},
	actionHandlers: {
		[NOW_BUTTON_CLICKED]: ({dispatch, action}) => {
			const { event } = action.payload;
			dispatch(event);
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on a button labeled secondary.
		 * @type {{value: object}}
		 * @uib.description Dispatched when the user clicks a secondary button
		 */
		[CONFIRMATION_MSG_SECONDARY_CLICKED]: {
			schema: {
				type: 'object'
			}
		},

		/**
		 * Dispatched when the user clicks on a button labeled primary.
		 * @type {{value: object}}
		 * @uib.description Dispatched when the user clicks a primary button
		 */
		[CONFIRMATION_MSG_PRIMARY_CLICKED]: {
			schema: {
				type: 'object'
			}
		}

	}
});
